.standup {
    margin-top: 30px;
    background-color: white;
    border-radius: 5px;
    color: gray;
}

.standup h4 {
    color: rgb(209, 99, 167);
    font-style: italic;
}
.header {
    background-color: gray;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    margin-top: -2px;
}

.header .header-icon {
    float: right;
    font-size: 20px;
    color: white;
    background-color: pink;
    padding: 2px 10px 0px 10px;
    border-radius: 5px;
    border: 2px solid white;
}

.header .header-icon:hover {
    color: gray;
}

.icons{
    border-top: 1px solid rgb(190, 187, 187);
    text-align: center;
    padding-bottom: 13px;
    padding-top: 15px;
}

.bottom-like{
    color: rgb(209, 99, 167);
    font-size: 30px;
    margin-right: 15px;
}

.bottom-dislike{
    color: rgb(34, 16, 27);
    font-size: 30px;
}

.bottom-dislike:hover {
    color: rgb(146, 8, 8);
}

.bottom-like:hover {
    color: rgb(8, 146, 54);
}
